*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

@font-face {
  font-family: DM Sans;
  src: url("DMSans-Regular.331f979e.ttf");
  font-weight: regular;
}

@font-face {
  font-family: DM Sans;
  src: url("DMSans-Medium.b8d1a1bf.ttf");
  font-weight: 500;
}

@font-face {
  font-family: DM Sans;
  src: url("DMSans-Bold.306cbdd3.ttf");
  font-weight: 700;
}

.mobile-menu .buttons-nav__item, .buttons-nav__item, .card__text, .card__img, .btn-top__svg, .btn--violet {
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

@keyframes btn-scroll {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.05);
  }

  100% {
    transform: scale(1);
  }
}

body {
  color: var(--main-text-color);
  margin: 0;
  font-family: DM Sans, sans-serif;
}

html {
  scroll-behavior: smooth;
}

main {
  padding-top: 40px;
  padding-bottom: 80px;
}

@media screen and (min-width: 768px) {
  main {
    padding-bottom: 100px;
  }
}

@media screen and (min-width: 1440px) {
  main {
    padding-bottom: 72px;
  }
}

h1, h2, h3, h4, h5, h6, p, ul, ol {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

ul, ol {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

:root {
  --main-color: #111;
  --secondary-color: #4f2ee8;
  --additional-text-color: #b4afaf;
  --primary-bcg: #fff;
  --additional-btn-color: #eac645;
  --scroll-color: #e0e0e0;
  --categories-color: rgba(17, 17, 17, .6);
  --dark-main-color: #fff;
  --dark-secondary-color: #4f2ee8;
  --dark-additional-text-color: #b4afaf;
  --dark-primary-bcg: #111;
  --dark-additional-bcg: #202024;
  --dark-additional-btn-color: #eac645;
  --dark-scroll-color: rgba(17, 17, 17, .6);
}

.container {
  min-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 375px) {
  .container {
    max-width: 375px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding: 0 24px;
  }
}

.hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.btn--violet {
  text-transform: uppercase;
  border: 2px solid var(--secondary-color);
  color: var(--main-color);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 40px;
  font-weight: 700;
}

.btn--violet:hover, .btn--violet:focus, .btn--violet:active {
  background-color: var(--secondary-color);
  color: var(--primary-bcg);
}

.btn--violet:focus {
  box-shadow: 0 4px 18px rgba(0, 0, 0, .15);
}

.btn-top {
  width: 54px;
  height: 54px;
  background-color: var(--secondary-color);
  z-index: 999;
  cursor: pointer;
  border: none;
  border-radius: 50%;
  animation: btn-scroll 1s infinite;
  position: fixed;
  bottom: 100px;
  right: 20px;
}

@media screen and (min-width: 768px) {
  .btn-top {
    bottom: 80px;
  }
}

.btn-top__svg {
  fill: var(--primary-bcg);
  animation: btn-scroll 1s infinite;
}

.btn-top:before {
  z-index: -1;
  content: "";
  width: 64px;
  height: 64px;
  background-color: rgba(79, 46, 232, .3);
  border-radius: 50%;
  position: absolute;
  top: -5px;
  left: -5px;
}

.btn-top.hidden {
  pointer-events: none;
  display: none;
}

.card {
  width: 100%;
  cursor: pointer;
}

@media screen and (max-width: 767px) {
  .card {
    min-width: 335px;
  }
}

@media screen and (min-width: 768px) {
  .card {
    width: calc(33.3333% - 16.6667px);
  }
}

@media screen and (min-width: 1440px) {
  .card {
    width: calc(20% - 19.2px);
  }
}

.card:hover .card__text, .card:focus .card__text, .card:active .card__text {
  transform: translateY(0);
}

.card:hover .card__img, .card:focus .card__img, .card:active .card__img {
  transform: scale(1.03);
}

.card__thumb {
  width: 100%;
  height: 485px;
  border-radius: 8px;
  position: relative;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .card__thumb {
    height: 316px;
  }
}

@media screen and (min-width: 1440px) {
  .card__thumb {
    height: 256px;
  }
}

.card__img {
  width: 100%;
  height: 100%;
  object-fit: fill;
  border-radius: 8px;
  display: block;
}

.card__text {
  width: 100%;
  background-color: var(--additional-btn-color);
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  padding-top: 40px;
  padding-bottom: 40px;
  position: absolute;
  bottom: 0;
  transform: translateY(101%);
}

@media screen and (min-width: 768px) {
  .card__text {
    padding-top: 26px;
    padding-bottom: 26px;
  }
}

.card__text-inner {
  text-transform: uppercase;
  text-align: center;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.2;
}

@media screen and (min-width: 768px) {
  .card__text-inner {
    font-size: 18px;
  }
}

@media screen and (min-width: 1440px) {
  .card__text-inner {
    font-size: 14px;
  }
}

.card__title {
  text-transform: uppercase;
  letter-spacing: -.04em;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-top: 14px;
  margin-bottom: 4px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.125;
  overflow: hidden;
}

.card__author {
  color: var(--additional-text-color);
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 1.2;
}

.error {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

@media screen and (min-width: 1440px) {
  .error {
    margin: 0 auto;
    padding-top: 140px;
  }
}

.error__text {
  text-align: center;
  letter-spacing: -.02em;
  color: rgba(0, 0, 0, .6);
  max-width: 350px;
  margin-bottom: 15px;
  font-size: 14px;
  line-height: 1.25;
}

@media screen and (min-width: 768px) {
  .error__text {
    margin-bottom: 20px;
    font-size: 18px;
  }
}

@media screen and (min-width: 768px) {
  .error__img {
    width: 322px;
  }
}

.header {
  min-width: 375px;
  z-index: 100;
  background-color: var(--primary-bcg);
  border: 1.5px solid var(--main-color);
  border-radius: 0 0 8px 8px;
  padding: 20px 0;
  position: relative;
}

@media screen and (min-width: 768px) {
  .header {
    padding: 12px 0;
  }
}

.header__box {
  justify-content: space-between;
  display: flex;
}

.header__nav {
  display: flex;
}

.header__logo {
  color: #000;
  align-items: center;
  gap: 8px;
  padding: 2px 0;
  font-weight: 700;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header__logo {
    margin-right: 40px;
  }
}

.header__buttons-nav {
  letter-spacing: -.04em;
  text-transform: uppercase;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.3;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header__buttons-nav {
    flex-direction: row;
    gap: 16px;
  }
}

@media screen and (max-width: 767px) {
  header .header__buttons-nav {
    display: none;
  }
}

.header__buttons-nav .buttons-nav__shop-list {
  fill: var(--main-color);
  align-items: center;
  gap: 8px;
  font-weight: 500;
  display: flex;
}

.buttons-nav__item {
  border-radius: 36px;
  padding: 8px 16px;
  display: block;
}

.buttons-nav__item:hover, .buttons-nav__item:focus {
  background-color: var(--additional-btn-color);
}

.header__buttons-auth {
  align-items: center;
  gap: 14px;
  display: flex;
}

.header__buttons-auth .buttons-auth__item {
  align-items: center;
  display: flex;
}

.header__buttons-auth .buttons-auth__item .user {
  color: var(--primary-bcg);
  letter-spacing: -.02em;
  background-color: var(--secondary-color);
  border: 0;
  border-radius: 36px;
  align-items: center;
  padding: 4px 12px;
  font-size: 22px;
  font-weight: 700;
  line-height: 1.3;
  display: flex;
}

@media screen and (max-width: 767px) {
  header .header__buttons-auth .buttons-auth__item .user {
    display: none;
  }
}

.header__buttons-auth .buttons-auth__item .user .user__arrow {
  fill: var(--primary-bcg);
  margin-left: 4px;
}

@media screen and (max-width: 767px) {
  .header__buttons-auth .buttons-auth__sign {
    display: none;
  }
}

.user__img {
  width: 37px;
  height: 37px;
  fill: var(--secondary-color);
  background-color: #f6f6f6;
  border-radius: 36px;
  margin-right: 9px;
  padding: 9px;
}

.buttons__switcher {
  height: 0;
  width: 0;
  visibility: hidden;
}

.buttons__switcher-label {
  cursor: pointer;
  text-indent: -9999px;
  width: 40px;
  height: 24px;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 100px;
  display: block;
  position: relative;
}

.buttons__switcher-label:after {
  content: "";
  width: 19px;
  height: 19px;
  background: radial-gradient(circle, #fff 15%, #fff 40%, #f0efef 40%, #f0efef 55%, #fff 55%), linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 90px;
  transition: all .25s ease-in;
  position: absolute;
  top: 2px;
  left: 2px;
}

.buttons__switcher:checked + .buttons__switcher-label {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
}

.buttons__switcher:checked + .buttons__switcher-label:after {
  left: calc(100% - 2px);
  transform: translateX(-100%);
}

.buttons__switcher-label:active:after {
  width: 25px;
}

.buttons-auth__sign-block {
  align-items: center;
  gap: 14px;
  display: flex;
}

.buttons-auth__sign {
  width: 184px;
  height: 48px;
  letter-spacing: -.04em;
  color: var(--primary-bcg);
  stroke: var(--additional-btn-color);
  background-color: #4f2ee8;
  border: 1.5px solid #111;
  border-radius: 18px;
  justify-content: space-between;
  align-items: center;
  padding: 13.5px 12.5px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.3;
  display: flex;
}

header .buttons-auth__sign {
  width: 104px;
}

.buttons-auth__menu {
  background-color: inherit;
  border: 0;
}

@media screen and (min-width: 768px) {
  .buttons-auth__menu {
    display: none;
  }
}

.buttons-auth__menu .menu__img {
  stroke: var(--main-color);
}

.mobile-menu {
  z-index: 99;
  background-image: url("mobile-menu-bg.4e97dcc6.png"), radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  background-position: bottom;
  background-repeat: no-repeat;
  padding-top: 104px;
  transition: transform .2s;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transform: translateY(-100%);
}

.mobile-menu .mobile-menu__box {
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.mobile-menu .mobile-menu__box .header__buttons-nav {
  align-items: flex-start;
}

.mobile-menu .mobile-menu__wrapper {
  height: 30%;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.mobile-menu .mobile-menu__user {
  letter-spacing: -.02em;
  color: var(--dark-main-color);
  align-items: center;
  gap: 9px;
  font-size: 22px;
  font-weight: 700;
  line-height: 1.3;
  display: flex;
}

.mobile-menu .buttons-nav__item {
  color: var(--primary-bcg);
  fill: var(--primary-bcg);
}

.mobile-menu .buttons-nav__item:hover, .mobile-menu .buttons-nav__item:focus {
  background-color: var(--additional-btn-color);
  color: var(--main-color);
  fill: var(--main-color);
}

.mobile-menu .buttons-auth__sign {
  width: 118px;
  background-color: var(--primary-bcg);
  color: var(--main-color);
  margin-bottom: 20px;
}

.showMenu {
  transform: translateY(0);
}

@media screen and (max-width: 767px) {
  .showButton {
    display: block;
  }
}

@media screen and (max-width: 767px) {
  .hideButton {
    display: none;
  }
}

body.lock {
  overflow: hidden;
}

.books__handler {
  flex-direction: column;
  gap: 40px;
  display: flex;
}

.category__list {
  flex-direction: row;
  gap: 25px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .category__list {
    gap: 25px;
  }
}

@media screen and (min-width: 1440px) {
  .category__list {
    gap: 24px;
  }
}

.category__list .card {
  display: none;
}

@media screen and (max-width: 767px) {
  .category__list .card:nth-child(1) {
    display: block;
  }
}

@media screen and (min-width: 768px) and (max-width: 1439px) {
  .category__list .card:nth-child(1), .category__list .card:nth-child(2), .category__list .card:nth-child(3) {
    display: block;
  }

  .category__list .card:nth-child(3) {
    gap: 0;
  }
}

@media screen and (min-width: 1440px) {
  .category__list .card {
    display: block;
  }
}

.category {
  flex-direction: column;
  display: flex;
}

.category__title {
  text-transform: uppercase;
  color: var(--additional-text-color);
  letter-spacing: .03em;
  margin-bottom: 18px;
  font-family: DM Sans;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.2;
}

.category__btn {
  align-self: flex-end;
  margin-top: 8px;
  padding: 14px 28px;
  font-family: DM Sans;
  font-size: 14px;
  font-weight: 700;
}

@media screen and (min-width: 768px) {
  .category__btn {
    margin-top: 34px;
  }
}

@media screen and (min-width: 1440px) {
  .category__btn {
    margin-top: 32px;
  }
}

.books {
  min-width: 335px;
  width: 100%;
}

.books--flex {
  flex-direction: column;
  gap: 40px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .books--flex {
    gap: 100px;
  }
}

@media screen and (min-width: 1440px) {
  .books--flex {
    flex-direction: row;
    gap: 40px;
  }
}

.books__title {
  text-transform: capitalize;
  letter-spacing: -.04em;
  margin-bottom: 40px;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.1875;
}

.books__title-last {
  color: var(--secondary-color);
}

@media screen and (min-width: 768px) {
  .books__title {
    font-size: 48px;
  }
}

.books__list {
  flex-direction: column;
  gap: 40px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .books__list {
    flex-flow: wrap;
    gap: 40px 25px;
  }
}

@media screen and (min-width: 1440px) {
  .books__list {
    column-gap: 24px;
  }
}

.categories {
  margin-bottom: 40px;
  padding-top: 40px;
}

@media screen and (min-width: 768px) {
  .categories {
    margin-bottom: 0;
    margin-right: 32px;
    padding-left: 12px;
  }
}

@media screen and (min-width: 1440px) {
  .categories {
    margin-bottom: 86px;
    margin-right: 0;
    padding-left: 4px;
  }
}

.categories__list {
  width: 100%;
  height: 228px;
  overflow-y: auto;
}

@media screen and (min-width: 375px) {
  .categories__list {
    width: 335px;
  }
}

@media screen and (min-width: 768px) {
  .categories__list {
    width: 337px;
    height: 472px;
  }
}

@media screen and (min-width: 1440px) {
  .categories__list {
    width: 356px;
  }
}

.categories__item {
  letter-spacing: -.02em;
  color: var(--categories-color);
  cursor: pointer;
  padding-bottom: 24px;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.125;
}

@media screen and (min-width: 768px) {
  .categories__item {
    padding-bottom: 32px;
    font-size: 18px;
    line-height: 1.33333;
  }
}

.categories__item:active {
  text-transform: uppercase;
  color: var(--secondary-color);
  font-weight: 700;
}

.support {
  width: 335px;
  height: 352px;
  background-color: var(--secondary-color);
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 10px 10px rgba(245, 245, 247, .1);
}

@media screen and (min-width: 768px) {
  .support {
    width: 335px;
    height: 472px;
  }
}

@media screen and (min-width: 1440px) {
  .support {
    width: 356px;
    height: 490px;
  }
}

.support__title {
  letter-spacing: -.04em;
  color: var(--primary-bcg);
  padding-top: 20px;
  padding-left: 40px;
  font-family: DM Sans;
  font-size: 28px;
  font-weight: 700;
  line-height: 1.14;
}

.support-logo {
  display: inline-block;
}

.buttons-slider {
  width: 38px;
  height: 38px;
  background-color: var(--primary-bcg);
  cursor: pointer;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-left: 152px;
  display: flex;
}

.buttons-slider__svg {
  fill: var(--secondary-color);
}

.flex-handler {
  flex-direction: row;
  display: flex;
}

.shopping-aside {
  display: none;
}

@media screen and (min-width: 1440px) {
  .shopping-aside {
    margin-right: 40px;
    display: block;
  }
}

.shoping-list_book {
  background: #fff;
  border: 2px solid rgba(79, 46, 232, .4);
  border-radius: 16px;
  margin-bottom: 20px;
  padding: 14px;
  display: flex;
  position: relative;
}

@media screen and (min-width: 768px) {
  .shoping-list_book {
    padding: 24px;
  }

  .shoping-list_book:last-child {
    margin-bottom: 40px;
  }
}

.delete-btn {
  transform: translate(-50% -50%);
  width: 28px;
  height: 28px;
  background-color: var(--secondary-color);
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 14px;
  right: 14px;
}

